import "./style.css";
import {useCallback, useEffect, useState} from "react";

import {loginBack} from "../../hooks/login";
import {isValidUser, useUserContext} from "../../hooks/useUserContext";

const btnMainColor = "#002d84";
const remToPx = (rem) =>
  rem * parseFloat(getComputedStyle(document.documentElement).fontSize);

function Home() {
  const {user} = useUserContext();

  const [rem4, rem8, rem12] = [remToPx(4), remToPx(8), remToPx(12)];
  const [btnStyle, setBtnStyle] = useState({
    marginTop: `${rem4}px`,
    background: btnMainColor,
  });

  const isValid = useCallback(() => {
    return isValidUser(user);
  }, [user]);

  const onHomeScroll = useCallback(() => {
    const scrolled = window.scrollY;

    if (scrolled >= rem12) {
      setBtnStyle({...btnStyle, background: "white", color: btnMainColor});
    } else {
      setBtnStyle({...btnStyle, background: btnMainColor, color: "white"});
    }

    if (scrolled < rem8) {
      setBtnStyle({
        ...btnStyle,
        marginTop: `${rem4 - scrolled}px`,
      });
    }
  }, [btnStyle, rem4, rem8, rem12]);

  useEffect(() => {
    window.addEventListener("scroll", onHomeScroll);
    return () => {
      window.removeEventListener("scroll", onHomeScroll);
    };
  }, [onHomeScroll]);

  return (
    <div className={"home"} id="home">
      <div
        onClick={() => loginBack()}
        style={isValid() ? {visibility: "hidden"} : btnStyle}
        className={"home-login-button round-btn"}
      >
        깃 헙 로그인
      </div>

      <div className={"home-intro-wrapper"}>
        <div className={"left"}>
          <h1 className={"home-intro-title"}>깃포가 뭔가요?</h1>
          <div
          // className="beautiful-title"
          // style={{fontSize: "1.4em", color: "white"}}
          >
            깃허브 연동을 통한 쉬운 포트폴리오 작성기 깃포!
            <br />
            그 동안 개발자들을 괴롭히던 포트폴리오 만들기는 이제 안녕~
            <br />
            우리 이제 포트폴리오는 깃포로해요!
            <br />
            <br />
            손쉽고, 빠르게! 포트폴리오를 작성해보세요
            <br />
            `미적감각과 시간은 없지만... 눈에 띄는 포트폴리오는 만들고
            시포(ppo)`
            <br />
            깃헙 꾸미고, 노션 포트폴리오 페이지 만들고, 디자인하고... pdf 파일
            따로 만들고... 세상이 우리 개발자들에게 요구하는게 너무 많지 않나요?
            <br />
            포(ppo)기 하지마세요! 깃포(Gitppo)와 함께하면 미적 감각과 시간이
            없어도, 예쁘고 눈에 띄는 포트폴리오를 만들 수 있습니다.
            <br />
            깃헙 계정으로 로그인하고, 추가 정보만 입력하면, 자동으로 눈에 띄는
            예쁜 포트폴리오가 완성!
            <br />
            깃포(Gitppo)는 한양대학교 멋사 8기와 9기가 만나 만든 프로젝트로,
            전체 멋사 해커톤에서 수상하며 시작된 프로젝트입니다.
            <br />
            저희의 귀여운 미니_사이드 프로젝트 깃포(Gitppo)에 대한 피드백과
            요청사항은 언제든 환영입니다 ;) 아! 사이트 곳곳에 숨겨진
            깃포(Gitppo)를 운영하는 하마🦛🦛들의 소개도 찾아보세요!
          </div>
        </div>
        <div className={"right"}>
          <h1 className={"home-intro-title"}>어떻게 써요?</h1>
          <div>
            Step 1. Github 아이디로 로그인!
            <br />
            Step 2. 포트폴리오 추가 버튼을 ❗️CLICK❗️ 이미 존재하는
            포트폴리오를 수정할 경우 리스트에서 선택!
            <br />
            Step 3. 불러온 Repository 목록들 중 포트폴리오에 추가하길 원하는
            Repository 만 선택!
            <br />
            Step 4. Repository 별로 제목, README, 기간, 역할, 기술스택 등 넣고
            싶은 내용만 입력하기😄
            <br />
            Step 5. 이제 개인 정보를 입력할 차례! 학력, 경력 등 넣으면 좋은
            정보를 입력해주세요. 이전 포트폴리오에서 데이터를 가져올 수도
            있답니다😉
            <br />
            Step 6. 마지막으로 포트폴리오의 템플릿을 선택하면 완료 🙆‍♂️ 링크를
            공유할지도 선택할 수 있어요
            <br />
            🦛 생성한 포트폴리오를 자소서에 추가하거나 서로 공유해보세요 🦛
          </div>
        </div>
      </div>
      <div className={"large-text-back"}>깃-포</div>
    </div>
  );
}

export default Home;
